import styled from 'styled-components'

const margin = "8.1rem";

export const HeaderStyle = styled.header`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: calc(100vw - ${margin*2});
  padding: 0px ${margin};
  background-image: url(/static/images/home/gradient.png);
  background-size: contain;
  height: 115px;
  color: white;

  .title {
    font-size: 32px;
    font-weight: 500;
    margin: 0px;
  }

  .nav {
    flex: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    &__link {
      margin: 0px;
      font-size: 16px;
      margin-left: 52px;
      cursor: pointer;
      &:hover {
        font-weight: bold;
      }
    }
    &__right, &__left {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      align-content: center;
    }
  }
`;



